import {FontAwesomeIcon} from '@fortawesome/react-fontawesome';
import {faBars, faSync} from '@fortawesome/free-solid-svg-icons';

import headerLogo from './images/logo-roots-black.svg';
import './styles/Header.css';

const Header = () => {
  return <header id='outer-header' className='container'>
    <header id='inner-header'>
      <FontAwesomeIcon className='header-icons' icon={faBars} fixedWidth/>
      <img id='header-logo' src={headerLogo} alt='ROOTS'/>
      <FontAwesomeIcon className='header-icons' icon={faSync} fixedWidth/>
    </header>
  </header>;
};

export default Header;
