import {useState, useEffect} from 'react';

import http from './http-common';
import Button from "./Button";

import './styles/UploadForm.css';

const UploadForm = () => {
  const [uploads, updateUploads] = useState([]);
  const [isUploading, setUploading] = useState(false);
  const [filesUploaded, setUploaded] = useState(false);

  const sendUpload = (file, onUploadProgress) => {
    const formData = new FormData();
    formData.append('file', file);

    return http.post('/api/uploadFile', formData, {
      headers: {
        'Content-Type': 'multipart/form-data'
      },
      onUploadProgress
    });
  };

  useEffect(() => {
    if (isUploading) {
      const uploadComplete = uploads.every((currVal) => currVal.progress === 100);
      if (uploadComplete) {
        setUploading(false);
        setUploaded(true);
      }
    }
  }, [uploads]);

  useEffect(() => {
    if (isUploading) {
      const filesToUpload = [...uploads];
      for (const [idx, upload] of filesToUpload.entries()) {
        sendUpload(upload.file, (event) => {
          const updatedUploadInfo = [...uploads];
          updatedUploadInfo[idx].progress = Math.round(100 * event.loaded / event.total);
          updateUploads(updatedUploadInfo);
        })
            .then(() => {
              const updatedUploadInfo = [...uploads];
              updatedUploadInfo[idx].uploadSuccess = true;
              updateUploads(updatedUploadInfo);
            })
            .catch(() => {
              const updatedUploadInfo = [...uploads];
              updatedUploadInfo[idx].uploadSuccess = false;
              updateUploads(updatedUploadInfo);
            });
      }
    }
  }, [isUploading]);

  const handleFormSubmit = (event) => {
    event.preventDefault();
    setUploading(true);
  };

  const handleUploadSelect = (event) => {
    const filesToUpload = [];
    for (const file of event.target.files) {
      filesToUpload.push({
        file: file,
        progress: 0,
        uploadSuccess: null
      });
    }
    updateUploads(filesToUpload);
    setUploaded(false);
  };

  const handleClearClick = () => {
    document.getElementById('upload-form').reset();
    updateUploads([]);
    setUploaded(false);
  };

  return <main id='upload-form-main' className='container'>
    <form id='upload-form' encType='multipart/form-data' onSubmit={handleFormSubmit}>
      <div id='form-header'>
        <div id='form-button-group'>
          <input type='file' id='upload-input' accept='image/*' multiple onChange={handleUploadSelect} disabled={isUploading}/>
          <label htmlFor='upload-input' id='upload-input-label' className='button'>Select Images</label>
          <Button text='Clear All' onClick={handleClearClick} disabled={uploads.length === 0 || isUploading}/>
          <Button text='Upload' type='submit' disabled={uploads.length === 0 || filesUploaded || isUploading}/>
        </div>
        {uploads.length > 0 ? <div id='selection-count'>{
          isUploading || filesUploaded ? `${uploads.filter(upload => upload.progress === 100).length} / ${uploads.length} uploads complete` :
              `${uploads.length} ${uploads.length === 1 ? 'image' : 'images'} selected`
        }</div> : <></>}
      </div>
      <div id='file-list'>{
        uploads.length === 0 ? <div id='no-file-text'>No files currently selected for upload.</div> : uploads.map((upload) => {
          return <article key={upload.file.name} className='upload-preview'>
            <img src={URL.createObjectURL(upload.file)} alt={upload.file.name}/>
            <div className='preview-divs'>
              <div>{upload.file.name}</div>
              {upload.uploadSuccess !== false && (isUploading || filesUploaded) ? <div className='progress-status'>
                <div className='progress-text'>{upload.progress}%</div>
                <div className='progress-bar'><div style={{width: `${upload.progress}%`}} /></div>
              </div> : <></>}
              {upload.uploadSuccess !== null ? <div>
                {upload.uploadSuccess ? 'Upload successful.' : 'Upload failed.'}
              </div> : <></>}
            </div>
          </article>;
        })
      }</div>
    </form>
  </main>;
};

export default UploadForm;
